@charset "UTF-8";
@font-face {
  font-family: "Fontello";
  src: url("../fonts/fontello.eot?26533562");
  src: url("../fonts/fontello.eot?26533562#iefix") format("embedded-opentype"), url("../fonts/fontello.woff2?26533562") format("woff2"), url("../fonts/fontello.woff?26533562") format("woff"), url("../fonts/fontello.ttf?26533562") format("truetype"), url("../fonts/fontello.svg?26533562#fontello") format("svg");
  font-weight: normal;
  font-style: normal;
}
[class^=icon-]:before,
[class*=" icon-"]:before {
  font-family: "Fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-email:before {
  content: "";
}

.button-social * {
  box-sizing: border-box;
}

.button-social {
  display: inline-flex;
  cursor: pointer;
  padding: 7px 10px;
  margin: 3px 1.5px;
  border-radius: 3px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
}

.button-social:hover {
  opacity: 0.9;
}

.button-social__square_edges {
  border-radius: 0;
  -moz-border-radius: 0;
  -webkit-border-radius: 0;
}

.email__design__flat {
  background-color: #414141;
  color: #fefefe;
}

.email__design__gradient {
  background-image: linear-gradient(to bottom, #414141, dimgray);
  background-image: -moz-linear-gradient(to bottom, #414141, dimgray);
  background-image: -o-linear-gradient(to bottom, #414141, dimgray);
  background-image: -webkit-linear-gradient(to bottom, #414141, dimgray);
  background-image: -ms-linear-gradient(to bottom, #414141, dimgray);
  color: #fefefe;
}

.email__design__outline {
  background-color: #fefefe;
  border: 1px solid #414141;
  color: #414141;
}

.title-social {
  margin-left: 6px;
}